import { Button } from 'ether-ui';
import { EditIcon } from 'ether-ui/icons';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Template } from '../../../types/template';
import { CampaignAddIcon } from '../../assets/icons';
import PreviewTemplate from './PreviewTemplate';
import { TemplateDetails } from './TemplateDetails';

interface TemplateCardProps {
    template: Template;
}

export const TemplateCard: React.FC<TemplateCardProps> = ({ template }) => {
    const navigate = useNavigate();

    const cardActions = (
        <div className="flex items-center gap-1">
            {template.status === 'approved' && (
                <Button
                    variant="success"
                    layout="plain"
                    size="sm"
                    leftIcon={CampaignAddIcon}
                    onClick={() => navigate(`/campaigns/create?templateId=${template.externalId}`)}
                    title="Create Campaign"
                />
            )}
            {template.status === 'draft' && (
                <Button
                    variant="default"
                    layout="plain"
                    leftIcon={EditIcon}
                    size="sm"
                    onClick={() => navigate(`/templates/${template.id}/edit`)}
                    title="Edit Template"
                />
            )}
        </div>
    );

    return (
        <div className="bg-white border border-gray-200 rounded-xl overflow-hidden shadow-sm hover:shadow-md transition-shadow">
            <PreviewTemplate template={template} />
            <div className="p-4">
                <TemplateDetails template={template} variant="card" showActions actions={cardActions} />
            </div>
        </div>
    );
};
