import { useEffect } from 'react';
import { useIntegrationStore } from '../../services/integration';
import { useTemplateStore } from '../../services/template';
import { TemplateFilters } from './TemplateFilters';
import { TemplateList } from './TemplateList';

function Templates() {
    const { loadTemplates, filters, templates, isLoading } = useTemplateStore();
    const { loadIntegrations, integrations } = useIntegrationStore();

    useEffect(() => {
        loadTemplates(filters);
        loadIntegrations();
    }, []);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-2xl font-bold text-gray-900">Templates</h2>
                    <p className="text-gray-600 mt-1">Manage your message templates across all channels</p>
                </div>
                {/* <Button onClick={() => navigate('/templates/create')}>Create Template</Button> */}
            </div>

            <TemplateFilters integrations={integrations} />

            <TemplateList templates={templates} isLoading={isLoading} />
        </div>
    );
}

export default Templates;
