import { Dropdown } from 'ether-ui';
import React from 'react';
import { Integration } from '../../../types/integration';
import { useTemplateStore } from '../../services/template';

interface TemplateFiltersProps {
    integrations: Integration[];
}

export const TemplateFilters: React.FC<TemplateFiltersProps> = ({ integrations }) => {
    const { filters, setFilters } = useTemplateStore();

    const statusOptions = [
        { label: 'All Status', value: '' },
        { label: 'Draft', value: 'draft' },
        { label: 'Pending', value: 'pending' },
        { label: 'Approved', value: 'approved' },
        { label: 'Rejected', value: 'rejected' },
    ];

    const typeOptions = [
        { label: 'All Types', value: '' },
        { label: 'Email', value: 'email' },
        { label: 'Gupshup', value: 'gupshup' },
        { label: 'WhatsApp', value: 'whatsapp' },
        { label: 'SMS', value: 'sms' },
    ];

    const integrationOptions = [
        { label: 'All Integrations', value: '' },
        ...integrations.map((int) => ({ label: int.name, value: int.id })),
    ];

    return (
        <div className="bg-white rounded-lg border border-gray-200 p-4">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Status</label>
                    <Dropdown
                        options={statusOptions}
                        value={filters.status || ''}
                        onChange={({ value }) => setFilters({ ...filters, status: value as any })}
                        placeholder="Select status"
                    />
                </div>
                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Type</label>
                    <Dropdown
                        options={typeOptions}
                        value={filters.type || ''}
                        onChange={({ value }) => setFilters({ ...filters, type: value as any })}
                        placeholder="Select type"
                    />
                </div>
                <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Integration</label>
                    <Dropdown
                        options={integrationOptions}
                        value={filters.integrationId || ''}
                        onChange={({ value }) => setFilters({ ...filters, integrationId: value })}
                        placeholder="Select integration"
                    />
                </div>
            </div>
        </div>
    );
};
